      return this.devices.filter(device => device.status === 'maintenance').length;
    },
    warningDevices() {
      return this.devices.filter(device => device.status === 'warning').length;
    },
    
    // Á≠õÈÄâËÆæÂ§á
    filteredDevices() {
      let result = this.devices;
      
      // ÊêúÁ¥¢ËøáÊª§
      if (this.searchQuery) {
        const query = this.searchQuery.toLowerCase();
        result = result.filter(device => 
          device.id.toLowerCase().includes(query) ||
          device.name.toLowerCase().includes(query)
        );
      }
      
      // Áä∂ÊÄÅËøáÊª§
      if (this.statusFilter !== 'all') {
        result = result.filter(device => device.status === this.statusFilter);
      }
      
      return result;
    },
    
    // ÂàÜÈ°µËÆæÂ§á
    paginatedDevices() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredDevices.slice(startIndex, startIndex + this.itemsPerPage);
    },
    
    // ÊÄªÈ°µÊï∞
    totalPages() {
      return Math.ceil(this.filteredDevices.length / this.itemsPerPage);
    },
    
    // Â∑•‰Ωú‰∏≠ËÆæÂ§áÂàóË°®
    workingDevicesList() {
      return this.devices.filter(device => device.status === 'working');
    }
  },
  
  watch: {
    // ÁõëÂê¨ÈÄâ‰∏≠ÁöÑÁõëÊéßËÆæÂ§áÂèòÂåñ
    selectedDeviceForMonitoring(newVal) {
      if (newVal) {
        this.updateCurrentDeviceData();
      } else {
        this.currentDeviceData = null;
      }
    }
  },
  
  mounted() {
    // ËÆæÁΩÆÂÆûÊó∂Êï∞ÊçÆÊõ¥Êñ∞ÂÆöÊó∂Âô®
    this.updateTimer = setInterval(() => {
      if (this.selectedDeviceForMonitoring) {
        this.updateCurrentDeviceData();
      }
    }, 3000);
  },
  
  beforeUnmount() {
    // Ê∏ÖÁêÜÂÆöÊó∂Âô®
    if (this.updateTimer) {
      clearInterval(this.updateTimer);
    }
  },
  
  methods: {
    // Áä∂ÊÄÅÊñáÊú¨
    getStatusText(status) {
      const statusMap = {
        working: '‰Ωú‰∏ö‰∏≠',
        ready: 'ÂæÖÂëΩ',
        charging: 'ÂÖÖÁîµ‰∏≠',
        maintenance: 'Áª¥Êä§‰∏≠',
        warning: 'ÂºÇÂ∏∏'
      };
      return statusMap[status] || status;
    },
    
    // ÁîµÊ±†Áä∂ÊÄÅÁ±ª
    getBatteryClass(battery) {
      if (battery > 70) return 'high';
      if (battery > 30) return 'medium';
      return 'low';
    },
    
    // Ê†ºÂºèÂåñÊó•Êúü
    formatDate(date) {
      if (!date) return '';
      const d = new Date(date);
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')} ${String(d.getHours()).padStart(2, '0')}:${String(d.getMinutes()).padStart(2, '0')}`;
    },
    
    // Ê†ºÂºèÂåñÊó∂Èïø
    formatDuration(hours) {
      const h = Math.floor(hours);
      const m = Math.round((hours - h) * 60);
      return `${h}h ${m}m`;
    },
    
    // ËÆæÂ§áÊìç‰Ωú
    addNewDevice() {
      const newId = 'DR' + String(this.devices.length + 1).padStart(3, '0');
      const newDevice = {
        id: newId,
        name: 'Êñ∞Êó†‰∫∫Êú∫' + newId,
        model: 'Êú™ÊåáÂÆö',
        status: 'ready',
        battery: 100,
        lastActivity: new Date(),
        operationHours: 0,
        purchaseDate: new Date().toISOString().split('T')[0],
        serialNumber: 'SN' + new Date().toISOString().split('T')[0].replace(/-/g, '') + String(this.devices.length + 1).padStart(3, '0'),
        maxLoad: 10,
        maxFlightTime: 30,
        maxSpeed: 15,
        efficiency: 80,
        controlRange: 10
      };
      this.devices.push(newDevice);
    },
    
    viewDeviceDetails(device) {
      this.selectedDevice = device;
      this.showDeviceModal = true;
    },
    
    closeDeviceModal() {
      this.showDeviceModal = false;
      this.selectedDevice = null;
    },
    
    startDevice(device) {
      device.status = 'working';
      device.lastActivity = new Date();
    },
    
    stopDevice(device) {
      device.status = 'ready';
      device.lastActivity = new Date();
    },
    
    scheduleMaintenance(device) {
      device.status = 'maintenance';
      device.lastActivity = new Date();
      
      // Ê∑ªÂä†Áª¥Êä§ËÆ∞ÂΩï
      const newRecord = {
        id: this.maintenanceRecords.length + 1,
        deviceName: device.name,
        type: 'ËÆ°ÂàíÁª¥Êä§',
        date: new Date(),
        description: `ËÆæÂ§á${device.name}ËÆ°ÂàíÁª¥Êä§`,
        outcome: 'success',
        technician: 'Á≥ªÁªüËá™Âä®'
      };
      this.maintenanceRecords.unshift(newRecord);
    },
    
    deleteDevice(id) {
      if (confirm('Á°ÆÂÆöË¶ÅÂà†Èô§Ê≠§ËÆæÂ§áÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§çÔºÅ')) {
        this.devices = this.devices.filter(device => device.id !== id);
        // Â¶ÇÊûúÊ≠£Âú®ÁõëÊéßÁöÑËÆæÂ§áË¢´Âà†Èô§ÔºåÊ∏ÖÈô§ÁõëÊéß
        if (this.selectedDeviceForMonitoring === id) {
          this.selectedDeviceForMonitoring = '';
        }
      }
    },
    
    // Êõ¥Êñ∞ÂΩìÂâçËÆæÂ§áÂÆûÊó∂Êï∞ÊçÆ
    updateCurrentDeviceData() {
      const device = this.devices.find(d => d.id === this.selectedDeviceForMonitoring);
      if (device) {
        // Ê®°ÊãüÂÆûÊó∂Êï∞ÊçÆ
        this.currentDeviceData = {
          longitude: 116.3 + (Math.random() - 0.5) * 0.01,
          latitude: 39.9 + (Math.random() - 0.5) * 0.01,
          altitude: Math.floor(Math.random() * 100 + 50),
          speed: Math.floor(Math.random() * 10 + 5),
          heading: Math.floor(Math.random() * 360),
          temperature: Math.floor(Math.random() * 10 + 20),
          humidity: Math.floor(Math.random() * 30 + 40),
          pressure: Math.floor(Math.random() * 50 + 980),
          signalStrength: Math.floor(Math.random() * 20 + 80),
          taskProgress: Math.floor(Math.random() * 100),
          currentTask: 'ÂÜúËçØÂñ∑Ê¥í',
          completedArea: Math.floor(Math.random() * 50 + 20),
          remainingTime: Math.floor(Math.random() * 60 + 20),
          motorStatus: 'normal',
          sensorStatus: 'normal',
          gpsStatus: 'normal',
          cameraStatus: 'normal'
        };
      }
    },
    
    // ÁªÑ‰ª∂Áä∂ÊÄÅÊñáÊú¨
    getComponentStatusText(status) {
      const statusMap = {
        normal: 'Ê≠£Â∏∏',
        warning: 'Ë≠¶Âëä',
        error: 'ÂºÇÂ∏∏',
        unknown: 'Êú™Áü•'
      };
      return statusMap[status] || 'Êú™Áü•';
    },
    
    // Áª¥Êä§ÁªìÊûúÊñáÊú¨
    getOutcomeText(outcome) {
      const outcomeMap = {
        success: 'ÊàêÂäü',
        warning: 'Ê≥®ÊÑè',
        error: 'Â§±Ë¥•'
      };
      return outcomeMap[outcome] || outcome;
    },
    
    // Êü•ÁúãÁª¥Êä§ËØ¶ÊÉÖ
    viewMaintenanceDetails(record) {
      alert(`Áª¥Êä§ËØ¶ÊÉÖ\n\nËÆæÂ§á: ${record.deviceName}\nÁ±ªÂûã: ${record.type}\nÊó•Êúü: ${this.formatDate(record.date)}\nÊäÄÊúØÂëò: ${record.technician}\nÁªìÊûú: ${this.getOutcomeText(record.outcome)}\n\nËØ¶ÁªÜÊèèËø∞:\n${record.description}`);
    }
  }
}
